import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { FormBuilder } from '@angular/forms';
import { Output, EventEmitter } from '@angular/core';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})


export class LoginComponent implements OnInit {
  @Output() updateHost = new EventEmitter<string>();
  @Output() updateHeaders = new EventEmitter<[]>();
  @Output() updateCookies = new EventEmitter<[]>();
  @Output() updatePrivileges = new EventEmitter<[]>();
  @Output() completed = new EventEmitter<boolean>();


  constructor(private formBuilder: FormBuilder, private http: HttpClient) { }


  loading: boolean;
  hosts = [
    { value: 'api.mist.com', viewValue: 'US - manage.mist.com' },
    { value: 'api.eu.mist.com', viewValue: 'EU - manage.eu.mist.com' },
    { value: 'api.gc1.mist.com', viewValue: 'GCP - manage.gc1.mist.com' }
  ];

  frmStepLogin = this.formBuilder.group({
    host: [''],
    credentials: this.formBuilder.group({
      email: [''],
      password: [''],
    }),
    token: [''],
  });


  ngOnInit() {
    this.frmStepLogin = this.formBuilder.group({
      host: ['api.mist.com'],
      credentials: this.formBuilder.group({
        email: [],
        password: [],
      }),
      token: [],
    });
  }



  check_host() {
    if (this.frmStepLogin.value.host != '') {
      return true;
    } else {
      return false;
    }
  }

  parse_response(data) {
    this.updateHost.emit(this.frmStepLogin.value.host)
    this.updateHeaders.emit(data.headers)
    this.updateCookies.emit(data.cookies);
    this.updatePrivileges.emit(data.data.privileges);
    this.completed.emit(true);
    this.loading = false;
  }

  reset_response() {
    this.updateHost.emit(null)
    this.updateHeaders.emit(null)
    this.updateCookies.emit(null);
    this.updatePrivileges.emit(null);
    this.completed.emit(false);
  }

  onSubmitCredentials() {
    this.reset_response();
    if (this.check_host()) {
      if (this.frmStepLogin.value.credentials.email != "" && this.frmStepLogin.value.credentials.password != "") {
        console.info("login/pwd")
      }
      this.loading = true;
      console.info(this.frmStepLogin.value.credentials)
      this.http.post<any>('/api/login/', { host: this.frmStepLogin.value.host, email: this.frmStepLogin.value.credentials.email, password: this.frmStepLogin.value.credentials.password }).subscribe({
        next: data => this.parse_response(data),
        error: error => console.error('There was an error!', error)
      })
    }
  }
  onSubmitToken() {
    this.reset_response();
    if (this.check_host()) {
      if (this.frmStepLogin.value.token != "") {
        console.info("token")
      }
      this.loading = true;
      console.info(this.frmStepLogin.value.token)
      this.http.post<any>('/api/login/', { host: this.frmStepLogin.value.host, token: this.frmStepLogin.value.token }).subscribe({
        next: data => this.parse_response(data),
        error: error => console.error('There was an error!', error)
      })

    }
  }

}

