import { Component, ViewChild, Input } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { LoginComponent } from '../login/login.component';
import { SitesComponent } from '../sites/sites.component';
import { GenerateComponent } from '../generate/generate.component';

import { MatStepper } from '@angular/material/stepper';

@Component({
  selector: 'stepper-component',
  templateUrl: './stepper.component.html',
  styleUrls: ['./stepper.component.css']
})

export class StepperComponent {
  @ViewChild('stepper') stepper: MatStepper;

  @ViewChild('LoginComponent', { static: false }) LoginComponent: LoginComponent;
  @ViewChild('SitesComponent') SitesComponent: SitesComponent;
  @ViewChild('GenerateComponent') GenerateComponent: GenerateComponent;

  get frmStepSites() {
    return this.LoginComponent ? this.LoginComponent.frmStepLogin : null;
  }
  get frmStepVerticals() {
    return this.LoginComponent ? this.LoginComponent.frmStepLogin : null;
  }
  get frmStepTypes() {
    return this.LoginComponent ? this.LoginComponent.frmStepLogin : null;
  }
  get frmStepExamples() {
    return this.LoginComponent ? this.LoginComponent.frmStepLogin : null;
  }
  get frmStepGenerate() {
    return this.LoginComponent ? this.LoginComponent.frmStepLogin : null;
  }

  host: string = "";
  headers: [] = [];
  cookies: [] = [];
  privileges: [] = [];
  orgId: string = "";
  siteIds: [] = [];
  type: string = "";
  reportIds: [] = [];
  generationReports= [];
  loginIsCompleted: boolean = false;
  sites_completed: boolean = false;
  reportIsCompleted: boolean = false;
  generationIsCompleted: boolean = false;

  constructor(private fb: FormBuilder) { }

  // LOGIN
  loginHostUpdate(data: string) {
    this.host = data;
  }
  loginHeadersUpdate(data: []) {
    this.headers = data;
  }
  loginCookiesUpdate(data: []) {
    this.cookies = data;
  }
  loginPrivilegesUpdate(data: []) {
    this.privileges = data;
  }
  loginCompleted(completed: boolean) {
    this.loginIsCompleted = completed;
    setTimeout(() => {
      this.stepper.next()
    });
  }

  // SITES
  sitesUpdateOrgId(data: string) {
    this.orgId = data
  }
  sitesUpdateSiteIds(data: []) {
    this.siteIds = data
  } 
  sitesCompleted(completed: boolean) {
    this.sites_completed = completed;
  }

  // REPORT
  reportCompleted(completed: boolean) {
    this.reportIsCompleted = completed;
  }
  reportUpdateIds(reportIds: []) {
    this.reportIds = reportIds;
    this.generationReports = []
    this.siteIds.forEach(site =>{
      this.reportIds.forEach(report=>{
        this.generationReports.push({"org": "", "site": site["name"], "site_id": site["id"], "floor": "", "zone": "", "report": report, "progress": "0", "link": null})
      })
    })
  }

  // GENERATION
  generationCompleted(completed: boolean) {
    this.generationIsCompleted = completed
  }


}
