import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-sites',
  templateUrl: './sites.component.html',
  styleUrls: ['./sites.component.css']
})
export class SitesComponent implements OnInit {
  @Input() host: string;
  @Input() headers: [];
  @Input() cookies: [];

  @Output() updateSiteIds = new EventEmitter<string[]>();
  @Output() updateOrgIds = new EventEmitter<string>();
  @Output() completed = new EventEmitter<boolean>();

  constructor(private http: HttpClient) { }

  mspId: string;
  orgId: string;
  loading: boolean = false;
  empty: boolean = true;
  show: boolean = false;
  listMspDisplayed = [];
  listOrg = [];
  listOrgDisplayed = [];
  sites = [];
  siteIds = [];

  @Input("privileges")
  set privileges(privileges: []) {
    if (privileges) {
      var tmpOrgId = [];
      privileges.forEach(entry => {
        switch (entry["scope"]) {
          case "msp": {
            this.listMspDisplayed.push({ id: entry["msp_id"], name: entry["name"] });
            break;
          }
          case "org": {
            if (tmpOrgId.indexOf(entry["org_id"]) < 0) {
              tmpOrgId.push(entry["org_id"])
              if ("msp_id" in entry) {
                this.listOrg.push({ id: entry["org_id"], name: entry["name"], msp: entry["msp_id"] });
              } else {
                this.listOrg.push({ id: entry["org_id"], name: entry["name"], msp: null });
              }
            }
            break;
          }
          default: {
            break;
          }
        }
      })
      this.listOrgDisplayed = this.sortList(this.listOrg);
      if (this.listOrg.length == 1) {
        this.refreshSiteList(this.listOrg[0]["id"]);
      }
    }
  }

  // INIT
  ngOnInit(): void { 
    this.sendUpdate();
  }

  // SELECT MSP
  changeMsp(select: MatSelectChange) {
    this.mspId = select.value;
    this.listOrgDisplayed = [];
    if (this.mspId == null) {
      this.listOrgDisplayed = this.listOrg;
    } else {
      this.listOrg.forEach(entry => {
        if (this.mspId == entry['msp']) [
          this.listOrgDisplayed.push({ id: entry["id"], name: entry["name"] })
        ]
      })
      this.listOrgDisplayed = this.sortList(this.listOrgDisplayed);
    }
  }

  // SELECT ORG
  changeOrg(select: MatSelectChange) {
    this.refreshSiteList(select.value);
  }

  refreshSiteList(newOrgId: string) {
    if (this.orgId != newOrgId) {
      this.orgId = newOrgId;
      this.siteIds = [];
      this.sendUpdate()
      if (this.orgId != null) {
        this.loading = true;
        this.show = false;
        this.empty = false;
        this.http.post<any>('/api/sites/', { host: this.host, org_id: this.orgId, headers: this.headers, cookies: this.cookies }).subscribe({
          next: data => this.parseResponse(data),
          error: error => console.error('There was an error!', error)
        })
      } else if (this.orgId == null) {
        this.sites = [];
        this.loading = false;
        this.empty = true;
      }
    }
  }

  parseResponse(data) {
    this.sites = this.sortList(data.data);
    this.loading = false;
    this.show = true;
  }

  getSiteInfo(id: string){
    var site = {}
    this.sites.some(tmpSite => {
      if (tmpSite.id == id){
        site = {"id": tmpSite.id, "name": tmpSite.name}
        return true
      }
    })
    return site
  }

  getSiteIndex(id: string){
    var index = -1
    this.siteIds.some(tmpIndex => {
      index += 1
      return tmpIndex == id            
    })
    return index
  }

  // SELECT SITE
  changeSite(mcChange: MatCheckboxChange) {    
    if (mcChange.checked){
      var site = this.getSiteInfo(mcChange.source.name)
      this.siteIds.push(site)
    } else {
      var i = this.getSiteIndex(mcChange.source.name)
      if (i >= 0) {
        this.siteIds.splice(i, 1);
      }      
    }
    this.sendUpdate();
  }


  // COMMON
  sortList(data) {
    return data.sort(function (a, b) {
      var nameA = a.name.toUpperCase(); // ignore upper and lowercase
      var nameB = b.name.toUpperCase(); // ignore upper and lowercase
      if (nameA < nameB) {
        return -1;
      }
      if (nameA > nameB) {
        return 1;
      }
      return 0;
    })
  }

  sendUpdate(){
    this.updateOrgIds.emit(this.orgId);
    this.updateSiteIds.emit(this.siteIds);
    this.completed.emit(this.siteIds.length > 0)
  }
}
