import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {MatProgressBarModule} from '@angular/material/progress-bar';
import { HttpClient } from '@angular/common/http';

export interface Site {
  id: string,
  name: string
}
export interface Result {
  org: string;
  site: string;
  floor: string;
  zone: string;
  report: string;
  progress: string;
  link: string;
}

@Component({
  selector: 'app-generate',
  templateUrl: './generate.component.html',
  styleUrls: ['./generate.component.css']
})
export class GenerateComponent implements OnInit {
  
  @Input() headers : [];
  @Input() cookies: [];
  @Input() host: string;
  @Input() generationReports: Result[];
  @Input() siteIds: Site[];
  @Input() orgId: string;
  @Input() reportIds: [];

  @Output() completed = new EventEmitter<boolean>();
  
  constructor(private http: HttpClient) { }

  displayedColumns: string[] = ['org', 'site', 'report', 'progress', "link"];
  dataSource = [];

  progress = 0
  reportDone = 0
  inProgress = false
  finished = false
  ngOnInit(): void {
  }

  parseReponse(data){
    var index = data["index"]
    var result = data["result"]
    if (result == "success") {
      this.generationReports[index].progress = "2"
      this.generationReports[index].link = data["url"]
    } else {
      this.generationReports[index].progress = "-1"
      this.generationReports[index].link = data["error"]
    }
    this.reportDone += 1
    this.progress = (this.reportDone / this.generationReports.length) * 100
  }

  sendRequest(index){    
    if (index < this.generationReports.length){
      this.generationReports[index].progress = "1"
      this.http.post<any>('/api/reports/', {host: this.host, org_id: this.orgId, headers: this.headers, cookies: this.cookies, "index": index, "site_id": this.generationReports[index]["site_id"], "report": this.generationReports[index]["report"]}).subscribe({
        next: data => {
          this.parseReponse(data)
          this.sendRequest(index+1)
        },
        error: error => console.log(error),
      })      
    }
  }

  create(){   
    this.inProgress = false
    this.finished = true
    this.sendRequest(0)
  }

  start(){
    this.dataSource=[]
    this.inProgress = true
    this.create()
  }
  stop(){
    this.inProgress =false
  }

  goToLink(url: string){
    window.open(url, "_blank");
}
}
